<template>
  <detail :title="activeTab" fixedTitle>
    <div slot="header-right">
      <p class="record" @click="routerPush(activeRecord.path)">{{activeRecord.text}}</p>
    </div>
    <cube-tab-bar v-model="activeTab" show-slider slot="section" @change="barChange">
      <cube-tab v-for="(item, index) in tabs" :icon="item.icon" :label="item.label" :key="index"></cube-tab>
    </cube-tab-bar>
    <cube-tab-panels v-model="activeTab" slot="main">
      <cube-tab-panel v-for="(item, idx) in tabs" :key="idx" :label="item.label">
        <form-custom :model="item.model" :items="item.items" text-align="right"></form-custom>
        <submit-button @click.native="item.submit"></submit-button>
      </cube-tab-panel>
    </cube-tab-panels>
  </detail>
</template>

<script>
  export default {
    name: "upgrade",
    data() {
      return {
        activeTab: "申请商务中心",
        businessCenter: {
          level: "",
          safePsw: ""
        },
        orderCenter: {
          level: "",
          safePsw: ""
        },
        levelUp: {
          level: "",
          safePsw: ""
        }
      };
    },
    computed: {
      tabs() {
        return [
          {
            label: "申请商务中心",
            model: this.businessCenter,
            items: this.itemsBusinessCenter,
            submit: this.submitBusinessCenter,
            record: {
              text: "申请记录",
              path: "/record?title=申请记录"
            }
          },
          {
            label: "申请报单中心",
            model: this.orderCenter,
            items: this.itemsOrderCenter,
            submit: this.submitOrderCenter,
            record: {
              text: "申请记录",
              path: "/record?title=申请记录"
            }
          },
          {
            label: "账号升级",
            model: this.levelUp,
            items: this.itemsLevelUp,
            submit: this.submitLevelUp,
            record: {
              text: "升级记录",
              path: "/record?title=升级记录"
            }
          }
        ];
      },
      itemsBusinessCenter() {
        return [
          {
            type: "cube-select",
            modelKey: "level",
            label: "商务级别 ",
            props: {
              options: ["1", "2", "3"]
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      itemsOrderCenter() {
        return [
          {
            type: "cube-select",
            modelKey: "level",
            label: "报单级别 ",
            props: {
              options: ["1", "2", "3"]
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      itemsLevelUp() {
        return [
          {
            type: "cube-select",
            modelKey: "level",
            label: "新级别 ",
            props: {
              options: ["1", "2", "3"]
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      activeRecord() {
        const obj = this.tabs.find(item => item.label === this.activeTab);
        if (!obj) return {};
        return obj.record;
      }
    },
    methods: {
      init() {
        this.clear();
        this.getActiveTab();
      },
      barChange(val) {
        const idx = this.tabs.findIndex(item => item.label === val);
        this.$router.replace({
          query: {
            id: idx + 1
          }
        });
      },
      getActiveTab() {
        const tabId = this.$route.query.id || "1";
        this.activeTab = this.tabs[tabId - 1].label;
      },
      submitBusinessCenter() {
        console.log(JSON.stringify(this.businessCenter, null, 4));
      },
      submitOrderCenter() {
        console.log(JSON.stringify(this.orderCenter, null, 4));
      },
      submitLevelUp() {
        console.log(JSON.stringify(this.levelUp, null, 4));
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssUnknownTarget -->
<style scoped lang="scss">
  @import "~@/assets/scss/cube-tab-bar.scss";
</style>
