<template>
  <detail type="ordinary" title="个人资料">
    <div slot="main" class="personal-information">
      <form-custom :model="model" :items="items">
        <i slot="reputationIndex" class="iconfont iconstar" v-for="i in model.reputation" :key="i"></i>
        <div slot="walletQR" class="walletQR">添加</div>
      </form-custom>
      <submit-button></submit-button>
    </div>
  </detail>
</template>

<script>
  import {mapState} from "vuex";

  export default {
    name: "personal-information",
    data() {
      return {
        model: {
          username: "",
          accountNature: "主账号",
          reputation: 5,
          regTime: "2016-06-06 12:00:00",
          country: 0,
          email: "nobechinacs@gmail.com",
          accountBank: 0,
          account: "622848******3033",
          accountBankAddress: "深圳布吉支行",
          walletType: 0,
          walletAddress: "456123****1239"
        }
      };
    },
    computed: {
      countryList() {
        const list = ["中国", "日本", "新加坡", "韩国", "美国", "英国"];
        return list.map((text, value) => {
          return {
            value,
            text
          };
        });
      },
      accountBankList() {
        const list = ["中国银行", "中国农业银行", "中国工商银行"];
        return list.map((text, value) => {
          return {
            value,
            text
          };
        });
      },
      walletTypeList() {
        const list = ["BTC", "ETH", "ABC"];
        return list.map((text, value) => {
          return {
            value,
            text
          };
        });
      },
      items() {
        return [
          {
            type: "cube-input",
            modelKey: "username",
            label: "用户名",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "accountNature",
            label: "账号性质",
            props: {
              readonly: true
            }
          },
          {
            type: "slot",
            modelKey: "reputationIndex",
            label: "信誉指数"
          },
          {
            type: "cube-input",
            modelKey: "regTime",
            label: "注册时间",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-select",
            modelKey: "country",
            label: "国籍",
            props: {
              options: this.countryList
            }
          },
          {
            type: "cube-input",
            modelKey: "email",
            label: "邮箱地址",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-select",
            modelKey: "accountBank",
            label: "开户行",
            props: {
              options: this.accountBankList
            }
          },
          {
            type: "cube-input",
            modelKey: "account",
            label: "银行账号",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "accountBankAddress",
            label: "开户地址",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-select",
            modelKey: "walletType",
            label: "钱包类型",
            props: {
              options: this.walletTypeList
            }
          },
          {
            type: "cube-input",
            modelKey: "walletAddress",
            label: "钱包地址",
            props: {
              readonly: true
            }
          },
          {
            type: "slot",
            modelKey: "walletQR",
            label: "钱包二维码"
          }
        ];
      },
      ...mapState(["user"])
    },
    watch: {
      user: {
        immediate: true,
        deep: true,
        handler(n) {
          if (n) {
            this.model.username = n.username;
          }
        }
      }
    },
    methods: {
      init() {
      },
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .personal-information {
    @include cleanOverflow;
    padding: 0 15px;
    .form-custom {
      padding-top: 30px;
      .iconstar {
        font-size: 14px;
      }
      .walletQR {
        font-size: 14px;
      }
      ::v-deep {
        .form-custom-item-content, input {
          text-align: right;
        }
        .cube-input {
          input {
            padding-right: 0;
          }
        }
      }
    }
  }

</style>
