<template>
  <div class="login">
    <header>
      <app-header :border="false">
        <button slot="left" class="cancel active-action" @click="cancel">取消</button>
      </app-header>
      <div class="title">
        <i class="iconfont iconlogo"></i>
        <p>NOBE MINE</p>
      </div>
      <p class="desc">登录</p>
    </header>
    <main>
      <div class="form-wrapper">
        <form>
          <div class="form-item">
            <cube-input
              type="text"
              placeholder="邮箱或手机号"
              clearable
              v-model="model.username"></cube-input>
          </div>
          <div class="form-item">
            <cube-input
              type="password"
              placeholder="密码"
              clearable
              :eye="{open:false}"
              v-model="model.psw"></cube-input>
          </div>
        </form>
        <submit-button text="登录" :disabled="disabled" @click.native="submit"></submit-button>
        <div class="tips">
          <p @click="routerPush('/retrieve-password')">忘记密码</p>
        </div>
      </div>
      <footer>
        <p>还没有账号？<span @click="routerPush('/reg')">注册</span></p>
      </footer>
    </main>
  </div>
</template>

<script>
  import transition from "@/utils/transition";
  import {mapMutations} from "vuex";

  export default {
    name: "login",
    data() {
      return {
        model: {
          username: "",
          psw: ""
        }
      };
    },
    computed: {
      disabled() {
        const flag = Boolean(this.model.username && this.model.psw);
        return !flag;
      }
    },
    methods: {
      init() {
        this.clear();
      },
      cancel() {
        transition.set("slide-back", 0);
        this.routerPush("/");
      },
      submit() {
        this.setUser({
          username: this.model.username
        });
        this.routerPush(this.$route.query.redirect || "/");
      },
      ...mapMutations(["setUser"])
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  $padding: 25px;
  .login {
    @include backgroundWhite;
    display: flex;
    flex-direction: column;
    header {
      flex: 0 0 140px;
      padding: 0 $padding;
      .app-header {
        margin-bottom: 20px;
        .cancel {
          background-color: transparent;
          border: none;
          outline: none;
          font-size: 14px;
          color: $subFontColor;
          position: relative;
          left: -25px;
        }
      }
      .title {
        display: flex;
        align-items: center;
        font-size: 30px;
        position: relative;
        left: -5px;
        .iconfont {
          font-size: 34px;
        }
        .iconfont, p {
          font-family: DIN-Black-2, "Microsoft YaHei UI", cursive;
        }
      }
      .desc {
        font-size: 14px;
        color: $subFontColor;
        margin-top: 10px;
      }
    }
    main {
      height: 100%;
      overflow-x: hidden;
      padding: 50px $padding 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .form-wrapper {
        form {
          .form-item {
            margin-bottom: 15px;
            .cube-input {
              border-bottom: 1px solid $subFontColor;
              &:after {
                opacity: 0;
              }
              &.cube-input_active {
                border-color: $blue;
              }
              ::v-deep {
                input {
                  padding-left: 0;
                  padding-right: 0;
                  font-size: 16px;
                  &::placeholder {
                    color: $subFontColor;
                  }
                }
              }
            }
          }
        }
        .tips {
          p {
            font-size: 14px;
            color: $blue;
          }
        }
      }
      footer {
        flex: 0 0 50px;
        margin-top: $padding;
        p {
          font-size: 16px;
          color: $subFontColor;
          span {
            color: $blue;
          }
        }
      }
    }
  }

</style>
