<template>
  <detail title="绑定谷歌身份验证器">
    <div slot="main" class="google-verification">
      <p class="title">请妥善备份密钥以防遗失</p>
      <div class="qr" ref="qr"></div>
      <div class="text">
        <p>{{text}}</p>
        <p class="copy active-action" v-clipboard:copy="text" v-clipboard:success="onCopy">复制</p>
      </div>
      <p class="tips">保存二维码到手机或复制密钥到剪贴板可能会有安全风险，请妥善保存</p>
      <div class="submit-button">
        <submit-button text="下一步" @click.native="routerPush('/google-code')"></submit-button>
      </div>
    </div>
  </detail>
</template>

<script>
  import QRCode from "qrcodejs2";

  export default {
    name: "google-verification",
    data() {
      return {
        qr: null,
        text: "DHIODSDKJKLEWEWQEE"
      };
    },
    methods: {
      init() {
        this.createQrCode();
      },
      createQrCode() {
        const dom = this.$refs.qr;
        if (this.qr) {
          this.qr.makeCode(this.text);
          return;
        }
        this.qr = new QRCode(dom, {
          text: this.text, // 需要转换为二维码的内容
          width: dom.clientWidth,
          height: dom.clientHeight,
          colorDark: "#000000",
          colorLight: "#ffffff",
          correctLevel: QRCode.CorrectLevel.H
        });
      },
      onCopy() {
        this.$toast({
          type: "correct",
          text: "复制成功"
        });
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  .google-verification {
    text-align: center;
    .title {
      padding: 30px $padding;
      font-size: 16px;
      font-weight: bold;
    }
    .qr {
      width: 160px;
      height: 160px;
      margin: auto;
    }
    .text {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      font-weight: bold;
      margin-top: 30px;
      .copy {
        font-weight: normal;
        color: $blue;
        margin-left: 10px;
      }
    }
    .tips {
      font-size: 12px;
      line-height: 16px;
      color: $subFontColor;
      margin-top: 80px;
      padding: $padding;
    }
    .submit-button {
      padding: 0 $padding;
    }
  }

</style>
