<template>
  <div class="detail">
    <header>
      <div class="app-bar" :style="appBarStyle"></div>
      <div class="header-main">
        <i class="iconfont iconback" @click="back"></i>
        <div class="title" :class="{'transparent':!titleFlag&&!fixedTitle}">
          <p>{{title}}</p>
        </div>
        <div class="header-right">
          <slot name="header-right"></slot>
        </div>
        <p class="subtitle" :class="{'transparent':subtitleFlag}" v-if="!fixedTitle">{{title}}</p>
      </div>
    </header>
    <slot name="section"></slot>
    <main @scroll="scrollOrdinary">
      <!--ordinary-->
      <template v-if="type==='ordinary'">
        <p class="subtitle" :class="{'transparent':!subtitleFlag}" v-if="!fixedTitle">{{title}}</p>
        <slot name="main"></slot>
      </template>
      <!--scroll-->
      <scroll
        v-if="type==='scroll'"
        ref="scroll"
        v-bind="$attrs"
        v-on="$listeners"
        :pullingDown="pullingDown"
        :pullingUp="pullingUp"
        :scrollEvents="scrollEvents"
        @scroll="scroll">
        <div slot="content">
          <p class="subtitle" :class="{'transparent':!subtitleFlag}" v-if="!fixedTitle">{{title}}</p>
          <slot name="main"></slot>
        </div>
      </scroll>
    </main>
  </div>
</template>

<script>
  import {mapState} from "vuex";

  export default {
    name: "detail",
    props: {
      type: {
        type: String,
        default: "ordinary"
      },
      title: {
        required: true,
        type: String,
        default: ""
      },
      fixedTitle: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        scrollEvents: ["scroll", "before-scroll-start", "scroll-end"],
        posY: 0
      };
    },
    computed: {
      back() {
        return this.$listeners.back || this.$back;
      },
      titleFlag() {
        return this.posY < -50;
      },
      subtitleFlag() {
        return this.posY < 0;
      },
      // 不知道为啥 scroll 组件直接用 this.$listeners.onPullingDown 判断时会报错
      pullingDown() {
        return !!this.$listeners.onPullingDown;
      },
      pullingUp() {
        return !!this.$listeners.onPullingUp;
      },
      appScrollTarget() {
        return this.$refs.scroll.$el;
      },
      appBarStyle() {
        return `height:${this.statusBarHeight}px`;
      },
      ...mapState(["statusBarHeight"])
    },
    methods: {
      refreshScroll() {
        this.$refs.scroll.refreshScroll();
      },
      scroll(pos) {
        this.posY = pos.y;
      },
      scrollOrdinary(e) {
        this.posY = 0 - e.target.scrollTop;
      }
    },
    mounted() {
    }
  };
</script>

<style scoped lang="scss">
  .detail {
    @include backgroundWhite;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .transparent {
      opacity: 0;
    }
    > header {
      .app-bar {
        width: 100%;
      }
      .header-main {
        padding: 0 15px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        .iconback {
          font-size: 22px;
        }
        .header-right, .iconback {
          position: relative;
          z-index: 1;
        }
        .title {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          transition: 0.3s;
          p {
            font-size: 18px;
            font-weight: bold;
            line-height: 50px;
            white-space: nowrap;
            max-width: 50%;
            margin: auto;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
        .subtitle {
          position: absolute;
          top: 50px;
          left: 0;
        }
      }
    }
    .subtitle {
      font-size: 30px;
      font-weight: bold;
      padding: 10px 15px 0;
    }
    > main {
      width: 100%;
      height: 100%;
      overflow-x: hidden;
    }
  }
</style>
