<template>
  <detail
    title="奖金列表"
    type="scroll"
    ref="detail"
    :data="list"
    @onPullingDown="onPullingDown"
    @onPullingUp="onPullingUp">
    <div slot="main" class="bonus">
      <ul class="list">
        <li v-for="(model, index) in listModel" :key="index">
          <div class="item" v-for="(item, idx) in items" :key="idx">
            <p class="title">{{item.label}}：</p>
            <p class="content">{{model[item.value]}}</p>
          </div>
        </li>
      </ul>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "bonus",
    data() {
      return {
        list: []
      };
    },
    computed: {
      items() {
        return [
          {
            label: "日期",
            value: "date"
          },
          {
            label: "分享奖",
            value: "share"
          },
          {
            label: "贡献奖",
            value: "contribution"
          },
          {
            label: "见点奖",
            value: "seePoint"
          },
          {
            label: "培育奖",
            value: "nurture"
          },
          {
            label: "社区奖",
            value: "community"
          },
          {
            label: "互动奖",
            value: "interactive"
          }
        ];
      },
      listModel() {
        const model = {
          date: "2016-06-02",
          share: "10",
          contribution: "10",
          seePoint: "10",
          nurture: "10",
          community: "10",
          interactive: "10"
        };
        const list = new Array(20);
        list.fill(model);
        return list;
      }
    },
    methods: {
      init() {
      },
      // 刷新滚动高度
      refreshScroll() {
        this.$refs.detail.refreshScroll();
      },
      // 下拉刷新
      onPullingDown(updateFN) {
        setTimeout(() => {
          updateFN();
        }, 1000);
      },
      // 上拉加载
      onPullingUp(updateFN) {
        setTimeout(() => {
          updateFN();
        }, 1000);
      }
    }
  };
</script>

<style scoped lang="scss">
  .bonus {
    padding: $padding;
    .list {
      li {
        box-shadow: $boxShadow;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: $padding;
        border-radius: 5px;
        &:not(:last-of-type) {
          margin-bottom: $padding;
        }
        .item {
          display: flex;
          align-items: center;
          margin-bottom: 10px;
          &:nth-of-type(3n-2) {
            flex: 0 0 40%;
          }
          &:nth-of-type(3n-1) {
            flex: 0 0 30%;
          }
          &:nth-of-type(3n) {
            flex: 0 0 30%;
            justify-content: flex-end;
          }
          p {
            font-size: 14px;
            &.content {
              color: $blue;
              font-weight: bold;
            }
          }
        }
      }
    }
  }

</style>
