import Vue from "vue";
import VueRouter from "vue-router";
import transition from "@/utils/transition";
import {checkUser} from "@/utils/user";
import {setBarStyle} from "@/utils/bridge";
import store from "@/store";

Vue.use(VueRouter);

const views = require.context("../views", false, /.vue$/);
const viewsRouter = views.keys().map(key => {
  const path = key.substring(key.indexOf("/"), key.lastIndexOf(".vue"));
  const name = key.substring(key.indexOf("/") + 1, key.lastIndexOf(".vue"));
  return {
    path,
    name,
    component: views(key).default,
    meta: {
      title: name
    }
  };
});

const tabsChildren = require.context("../views/tabs/children", false, /.vue$/);
const tabsChildrenRouter = tabsChildren.keys().map(key => {
  const path = key.substring(key.indexOf("/"), key.lastIndexOf(".vue"));
  const name = key.substring(key.indexOf("/") + 1, key.lastIndexOf(".vue"));
  return {
    path,
    name,
    component: tabsChildren(key).default,
    meta: {
      title: name
    }
  };
});

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      redirect: "/assets"
    },
    {
      path: "/tabs",
      name: "tabs",
      component: () => import("../views/tabs/tabs.vue"),
      redirect: "/assets",
      children: tabsChildrenRouter
    },
    ...viewsRouter
  ],
  scrollBehavior(to, from, savedPosition) {
    store.commit("setIsBack", savedPosition !== null);
  }
});

router.onReady(res => {
  transition.ready();
});

router.beforeEach((to, from, next) => {
  // 重置 transition
  transition.set("slide-into");
  // 权限校验
  checkUser(to).then(() => {
    next();
  }).catch(() => {
    next({
      path: "/login",
      query: {
        redirect: to.fullPath
      }
    });
  });
  // 设置状态栏字体颜色
  setBarStyle(to);
});

Vue.prototype.$back = () => {
  // 设置 transition
  transition.set("slide-back", 0);
  router.back();
};

export default router;
