<template>
  <detail type="ordinary" title="玩家开户">
    <div slot="main" class="reg">
      <form-custom :model="model" :items="items"></form-custom>
      <submit-button @click.native="submit" text="提交"></submit-button>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "reg",
    data() {
      return {
        model: {
          username: "",
          level: "",
          psw: "",
          cfPsw: "",
          safePsw: "",
          cfSafePsw: "",
          email: "",
          country: 0,
          referrer: "",
          contactID: "",
          location: "",
          orderCenter: ""
        }
      };
    },
    computed: {
      items() {
        return [
          {
            type: "cube-input",
            modelKey: "username",
            label: "用户名",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-select",
            modelKey: "level",
            label: "级别",
            props: {
              options: [1, 2, 3, 4, 5, 6]
            }
          },
          {
            type: "cube-input",
            modelKey: "psw",
            label: "密码",
            props: {
              placeholder: "请输入",
              type: "password",
              clearable: true,
              eye: {
                open: false,
                reverse: false
              }
            }
          },
          {
            type: "cube-input",
            modelKey: "cfPsw",
            label: "确认密码",
            props: {
              placeholder: "请输入",
              type: "password",
              clearable: true,
              eye: {
                open: false,
                reverse: false
              }
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "交易密码",
            props: {
              placeholder: "请输入",
              type: "password",
              clearable: true,
              eye: {
                open: false,
                reverse: false
              }
            }
          },
          {
            type: "cube-input",
            modelKey: "cfSafePsw",
            label: "确认交易密码",
            props: {
              placeholder: "请输入",
              type: "password",
              clearable: true,
              eye: {
                open: false,
                reverse: false
              }
            }
          },
          {
            type: "cube-input",
            modelKey: "email",
            label: "邮箱地址",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-select",
            modelKey: "country",
            label: "国家",
            props: {
              options: this.countryList
            }
          },
          {
            type: "cube-input",
            modelKey: "referrer",
            label: "推荐人",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "contactID",
            label: "接点ID",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-select",
            modelKey: "location",
            label: "所在区位",
            props: {
              options: this.locationList
            }
          },
          {
            type: "cube-input",
            modelKey: "orderCenter",
            label: "报单中心",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          }
        ];
      },
      countryList() {
        return [
          {
            value: 0,
            text: "中国"
          },
          {
            value: 1,
            text: "日本"
          }
        ];
      },
      locationList() {
        return [
          {
            value: 0,
            text: "左区"
          },
          {
            value: 1,
            text: "右区"
          }
        ];
      }
    },
    methods: {
      init() {
      },
      submit() {
        console.log(JSON.stringify(this.model, null, 4));
      }
    }
  };
</script>

<style scoped lang="scss">
  .reg {
    @include cleanOverflow;
    padding: 30px 15px 0;
  }
</style>
