<template>
  <detail :title="activeTab" fixedTitle>
    <div slot="header-right">
      <p class="record" @click="routerPush(activeRecord.path)">{{activeRecord.text}}</p>
    </div>
    <cube-tab-bar v-model="activeTab" show-slider slot="section" @change="barChange">
      <cube-tab
        v-for="(item, index) in tabs"
        :icon="item.icon"
        :label="item.label"
        :key="index"></cube-tab>
    </cube-tab-bar>
    <cube-tab-panels v-model="activeTab" slot="main">
      <cube-tab-panel v-for="(item, idx) in tabs" :key="idx" :label="item.label">
        <form-custom :model="item.model" :items="item.items" text-align="right"></form-custom>
        <submit-button @click.native="item.submit"></submit-button>
      </cube-tab-panel>
    </cube-tab-panels>
  </detail>
</template>

<script>
  export default {
    name: "exchange",
    data() {
      return {
        activeTab: "USDN转ZCB",
        dataToZCB: {
          username: "user123",
          balance: 456,
          quantity: "",
          safePsw: ""
        },
        dataBusinessCenter: {
          username: "user123",
          balance: 456,
          businessCenter: "",
          quantity: "",
          safePsw: ""
        },
        dataSubAccount: {
          username: "user123",
          balance: 456,
          number: "",
          quantity: "",
          safePsw: ""
        },
        dataWalletExchange: {
          balance: 456,
          wallet: "",
          quantity: "",
          safePsw: ""
        }
      };
    },
    computed: {
      tabs() {
        return [
          {
            label: "USDN转ZCB",
            model: this.dataToZCB,
            items: this.itemsToZCB,
            submit: this.submitToZCB,
            record: {
              text: "转换记录",
              path: "/record?title=转换记录"
            }
          },
          {
            label: "转商务中心",
            model: this.dataBusinessCenter,
            items: this.itemsBusinessCenter,
            submit: this.submitBusinessCenter,
            record: {
              text: "转账记录",
              path: "/record?title=转账记录"
            }
          },
          {
            label: "主/子账号",
            model: this.dataSubAccount,
            items: this.itemsSubAccount,
            submit: this.submitSubAccount,
            record: {
              text: "转账记录",
              path: "/record?title=转账记录"
            }
          },
          {
            label: "兑换NBH",
            model: this.dataWalletExchange,
            items: this.itemsWalletExchange,
            submit: this.submitWalletExchange,
            record: {
              text: "转换记录",
              path: "/record?title=转换记录"
            }
          }
        ];
      },
      itemsToZCB() {
        return [
          {
            type: "cube-input",
            modelKey: "username",
            label: "用户名",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "balance",
            label: "USDN余额",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "quantity",
            label: "数量",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      itemsBusinessCenter() {
        return [
          {
            type: "cube-input",
            modelKey: "username",
            label: "用户名",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "balance",
            label: "USDN余额",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "businessCenter",
            label: "商务中心",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "quantity",
            label: "数量",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      itemsSubAccount() {
        return [
          {
            type: "cube-input",
            modelKey: "username",
            label: "用户名",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "balance",
            label: "USDN余额",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "number",
            label: "转入编号",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "quantity",
            label: "数量",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      itemsWalletExchange() {
        return [
          {
            type: "cube-input",
            modelKey: "balance",
            label: "USDN余额",
            props: {
              readonly: true
            }
          },
          {
            type: "cube-input",
            modelKey: "wallet",
            label: "钱包账号",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "quantity",
            label: "数量",
            props: {
              placeholder: "请输入",
              type: "text",
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "safePsw",
            label: "安全密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      activeRecord() {
        const obj = this.tabs.find(item => item.label === this.activeTab);
        if (!obj) return {};
        return obj.record;
      }
    },
    methods: {
      init() {
        this.clear();
        this.getActiveTab();
      },
      barChange(val) {
        const idx = this.tabs.findIndex(item => item.label === val);
        this.$router.replace({
          query: {
            id: idx + 1
          }
        });
      },
      getActiveTab() {
        const tabId = this.$route.query.id || "1";
        this.activeTab = this.tabs[tabId - 1].label;
      },
      submitToZCB() {
      },
      submitBusinessCenter() {
      },
      submitSubAccount() {
      },
      submitWalletExchange() {
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssUnknownTarget -->
<style scoped lang="scss">
  @import "~@/assets/scss/cube-tab-bar.scss";
</style>
