<template>
  <div class="setup">
    <app-header title="设置"></app-header>
    <main ref="scrollTarget" @scroll="scroll">
      <div class="account-info" @click="routerPush('/personal-information')">
        <div class="avatar">
          <img src="@/assets/images/soft.png">
        </div>
        <div class="content">
          <p class="title">账户信息</p>
          <div>
            <p>ID: {{user.username}}</p>
            <p>级别: M1</p>
          </div>
          <p class="reg-time">注册时间: 2020-02-22 14:00</p>
        </div>
      </div>
      <ul class="nav">
        <li class="active-action" @click="routerPush('/help')">
          <i class="iconfont iconhelp"></i>
          <p>帮助中心</p>
        </li>
        <li class="active-action" @click="routerPush('/announcement')">
          <i class="iconfont iconring"></i>
          <p>公告中心</p>
        </li>
        <li class="active-action" @click="routerPush('/google-verification')">
          <i class="iconfont iconverification"></i>
          <p>谷歌验证</p>
        </li>
      </ul>

      <div class="list-wrapper" v-for="(item, idx) in listWrapper" :key="idx">
        <p class="title">{{item.name}}</p>
        <ul>
          <li class="active-action" v-for="(it, i) in item.list" :key="i" @click="handleClick(it)">
            <p class="name">{{it.name}}</p>
            <div>
              <p>{{it.desc}}</p>
              <i class="iconfont iconarrowright"></i>
            </div>
          </li>
        </ul>
      </div>
    </main>
  </div>
</template>

<script>
  import mixinScrollToHeight from "@/utils/mixinScrollToHeight";
  import {mapState} from "vuex";

  export default {
    name: "setup",
    mixins: [mixinScrollToHeight],
    data() {
      return {
        languageID: 0
      };
    },
    computed: {
      listWrapper() {
        return [
          {
            name: "基本设置",
            list: this.basicList
          },
          {
            name: "协议与条款",
            list: this.protocolList
          }
        ];
      },
      basicList() {
        return [
          {
            name: "计价货币",
            desc: "人民币(¥)",
            type: "route",
            path: "/denomination-currency"
          },
          {
            name: "显示语言",
            desc: this.languageText,
            type: "route",
            path: "/language"
          },
          {
            name: "联系客服",
            desc: "nobechinacs@gmail.com"
          },
          {
            name: "邮箱绑定",
            type: "route",
            path: "/bind-email"
          },
          {
            name: "版本更新",
            desc: "V1.0.0",
            type: "action",
            action: this.versionUpdate
          }
        ];
      },
      protocolList() {
        return [
          {
            name: "用户协议",
            type: "route",
            path: "/protocol?title=用户协议"
          },
          {
            name: "隐私政策",
            type: "route",
            path: "/protocol?title=隐私政策"
          },
          {
            name: "免责声明",
            type: "route",
            path: "/protocol?title=免责声明"
          }
        ];
      },
      languageList() {
        const list = ["简体中文", "English"];
        return list.map((text, value) => {
          return {
            text,
            value
          };
        });
      },
      languageText() {
        const obj = this.languageList.find(item => item.value === this.languageID);
        return obj ? obj.text : "";
      },
      ...mapState(["user"])
    },
    methods: {
      init() {
      },
      handleClick(it) {
        if (it.type === "action") it.action();
        if (it.type === "route") this.routerPush(it.path);
      },
      pickLanguage() {
        this.$createPicker({
          title: "请选择",
          data: [this.languageList],
          selectedIndex: [this.languageID],
          onSelect: (selectedVal, selectedIndex, selectedText) => {
            this.languageID = selectedVal[0];
          }
        }).show();
      },
      versionUpdate() {
        this.$toast("已经是最新版本");
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  .setup {
    .account-info {
      @include backgroundWhite;
      box-shadow: $boxShadow;
      padding: 20px;
      margin: 15px;
      display: flex;
      align-items: center;
      border-radius: 5px;
      .avatar {
        flex: 0 0 70px;
        width: 70px;
        height: 70px;
        border-radius: 100%;
        overflow: hidden;
        margin-right: 15px;
        img {
          width: 100%;
        }
      }
      .content {
        .title {
          font-size: 16px;
          margin-bottom: 15px;
        }
        > div {
          display: flex;
          align-items: center;
          margin-bottom: 10px;
          p {
            font-size: 12px;
            opacity: 0.7;
            &:first-of-type {
              margin-right: 20px;
            }
          }
        }
        .reg-time {
          font-size: 12px;
          opacity: 0.7;
        }
      }
    }
    .nav {
      display: flex;
      align-items: flex-start;
      margin: 15px;
      li {
        flex: 1;
        @include backgroundWhite;
        box-shadow: $boxShadow;
        padding: 20px 0;
        text-align: center;
        border-radius: 5px;
        &:not(:last-of-type) {
          margin-right: 15px;
        }
        .iconfont {
          font-size: 30px;
          color: $blue;
        }
        p {
          font-size: 14px;
          margin-top: 15px;
        }
      }
    }
    .list-wrapper {
      .title {
        font-size: 16px;
        line-height: 45px;
        padding: 0 15px;
        opacity: 0.7;
      }
      ul {
        @include backgroundWhite;
        padding: 0 15px;
        box-shadow: $boxShadow;
        li {
          display: flex;
          align-items: center;
          justify-content: space-between;
          &:not(:last-of-type) {
            border-bottom: $border;
          }
          .name {
            font-size: 16px;
            line-height: 50px;
          }
          > div {
            display: flex;
            align-items: center;
            font-size: 14px;
            color: $subFontColor;
            p {
              margin-right: 10px;
            }
            .iconfont {
              font-size: 12px;
            }
          }
        }
      }
    }
  }

</style>
