<template>
  <detail title="财务明细" fixedTitle>
    <cube-scroll-nav-bar
      slot="section"
      :current="currentTab"
      :labels="nav.map(item => item.label)"
      :txts="nav.map(item => item.txt)"
      @change="changeHandler"/>
    <table-scroll slot="main" :tableHeader="tableHeader" :tableData="tableData">
      <template slot="amount" scope="scope">
        <div :class="{'green':scope.row.amount>0}">{{scope.row.amount>0?"+":""}}{{scope.row.amount}}</div>
      </template>
    </table-scroll>
  </detail>
</template>

<script>
  export default {
    name: "assets-detail",
    data() {
      return {};
    },
    computed: {
      nav() {
        const coins = ["USDT", "HUSD", "BTC", "ETH", "HT", "ALTS", "ABC"];
        return coins.map((txt, label) => {
          return {
            label,
            txt
          };
        });
      },
      currentTab() {
        const currentTab = this.nav[0];
        if (!currentTab) return "";
        return currentTab.label;
      },
      tableHeader() {
        return [
          {
            name: "奖金类型",
            key: "type"
          },
          {
            name: "时间",
            key: "time"
          },
          {
            name: "金额",
            key: "amount"
          }
        ];
      },
      tableData() {
        const txt = ["激活会员", "商务中心转入"];

        function getTxt() {
          return txt[Math.floor(Math.random() * 100) % 2];
        }

        function getAmount() {
          return Math.floor(Math.random() * 100 - 50);
        }

        const arr = [];
        for (let i = 0; i < 20; i++) {
          arr.push({
            type: getTxt(),
            time: "2020-06-06 12:00:00",
            amount: getAmount()
          });
        }
        return arr;
      }
    },
    methods: {
      init() {
      },
      changeHandler(cur) {
        this.current = cur;
      }
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .cube-scroll-nav-bar {
    border-bottom: $border;
    flex: 0 0 50px;
    ::v-deep {
      .cube-scroll-nav-bar-item {
        padding-top: 0;
        padding-bottom: 0;
        height: 50px;
        span {
          line-height: 50px;
        }
      }
      .cube-scroll-nav-bar-item_active {
        color: $blue;
        border-bottom: 2px solid $blue;
        box-sizing: border-box;
      }
    }
  }

  .table-scroll {
    ::v-deep {
      .title {
        li {
          font-size: 14px;
          font-weight: bold;
          padding: 0 10px;
          line-height: 40px;
          &:nth-of-type(1) {
            flex: 2;
          }
          &:nth-of-type(2) {
            flex: 3;
          }
          &:nth-of-type(3) {
            flex: 1;
          }
        }
      }
      .list-wrapper {
        li {
          div {
            font-size: 14px;
            padding: 12px 10px;
            &:nth-of-type(1) {
              flex: 2;
            }
            &:nth-of-type(2) {
              flex: 3;
            }
            &:nth-of-type(3) {
              flex: 1;
              color: $red;
              &.green {
                color: $green;
              }
            }
          }
        }
      }
    }
  }

</style>
