<template>
  <detail
    title="管理网"
    type="scroll"
    ref="detail"
    :data="list"
    @onPullingDown="onPullingDown"
    @onPullingUp="onPullingUp">
    <div slot="main" class="management-net">
      <div class="top">
        <div class="search-wrapper">
          <div class="input-box">
            <i class="iconfont iconsearch"></i>
            <cube-input type="text" placeholder="请输入会员ID" clearable></cube-input>
          </div>
          <p class="active-action">搜索</p>
        </div>
        <div class="btn-box">
          <button class="active-action">一键到左</button>
          <button class="active-action">一键到右</button>
        </div>
      </div>
      <ul class="list">
        <li v-for="item in 20" :key="item">
          <div class="content">
            <div class="avatar">
              <img src="@/assets/images/soft.png">
            </div>
            <div class="right">
              <div class="name">
                <p>用户名：user{{item}}</p>
              </div>
              <div class="info">
                <p>左区业绩：100</p>
                <p>右区业绩：0</p>
                <p>级别：M1</p>
                <p>注册时间：2012-02-02</p>
              </div>
            </div>
          </div>
        </li>
        <!--注册左区业绩-->
        <li>
          <div class="content reg">
            <div class="avatar">
              <img src="@/assets/images/soft.png">
            </div>
            <button class="active-action" @click="routerPush('/reg')">注册1.1</button>
          </div>
        </li>
        <!--注册右区业绩-->
        <li>
          <div class="content reg">
            <div class="avatar">
              <img src="@/assets/images/soft.png">
            </div>
            <button class="active-action" @click="routerPush('/reg')">注册1.2</button>
          </div>
        </li>
      </ul>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "management-net",
    data() {
      return {
        list: []
      };
    },
    methods: {
      init() {},
      // 刷新滚动高度
      refreshScroll() {
        this.$refs.detail.refreshScroll();
      },
      // 下拉刷新
      onPullingDown(updateFN) {
        setTimeout(() => {
          this.listLength = this.$options.data().listLength;
          updateFN();
        }, 1000);
      },
      // 上拉加载
      onPullingUp(updateFN) {
        setTimeout(() => {
          if (this.listLength < 50) this.listLength += 10;
          updateFN();
        }, 1000);
      }
    }
  };
</script>

<!--suppress CssUnknownTarget -->
<style scoped lang="scss">
  @import "~@/assets/scss/market-list.scss";

  .management-net {
    @include cleanOverflow;
    padding: $padding;
  }
  .top {
    .search-wrapper {
      display: flex;
      align-items: center;
      height: 40px;
      .input-box {
        width: 100%;
        display: flex;
        align-items: center;
        border: $border;
        height: 40px;
        padding: 0 10px;
        border-radius: 100px;
        margin-right: 10px;
        overflow: hidden;
        .iconfont {
          font-size: 20px;
        }
        .cube-input {
          width: 100%;
          font-size: 16px;
          &:after {
            opacity: 0;
          }
        }
      }
      p {
        font-size: 16px;
        white-space: nowrap;
      }
    }
    .btn-box {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 15px;
      button {
        flex: 1;
        height: 36px;
        @include backgroundBlue;
        border: none;
        outline: none;
        box-shadow: 1px 2px 3px 0 rgba(0, 0, 0, 0.4);
        &:not(:last-of-type) {
          margin-right: 15px;
        }
      }
    }
  }
  .list {
    margin-top: $padding;
    padding: 0;
    li {
      .content {
        &.reg {
          align-items: center;
          > button {
            @include backgroundBlue;
            font-size: 14px;
            outline: none;
            border: none;
            flex: 0 0 100px;
            width: 100px;
            height: 36px;
            box-shadow: 1px 2px 3px 0 rgba(0, 0, 0, 0.3);
          }
        }
      }
    }
  }

</style>
