<template>
  <detail
    ref="detail"
    type="scroll"
    :title="title"
    :data="list"
    @onPullingDown="onPullingDown"
    @onPullingUp="onPullingUp">
    <ul class="list" slot="main">
      <li class="active-action" v-for="(item, idx) in list" :key="idx">
        <img :src="item.icon" @load="refreshScroll">
        <div>
          <p class="name">{{item.name}} {{idx+1}}</p>
          <p class="desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </detail>
</template>

<script>
  export default {
    name: "software",
    data() {
      return {
        title: "",
        model: {
          icon: require("@/assets/images/soft.png"),
          name: "ChinaZ Arena",
          desc: "放置与挖矿，玩与赚"
        },
        listLength: 20
      };
    },
    computed: {
      list() {
        const arr = [];
        for (let i = 0; i < this.listLength; i++) {
          arr.push(this.model);
        }
        return arr;
      }
    },
    methods: {
      init() {
        this.title = this.$route.query.name;
      },
      // 刷新滚动高度
      refreshScroll() {
        this.$refs.detail.refreshScroll();
      },
      // 下拉刷新
      onPullingDown(updateFN) {
        setTimeout(() => {
          this.listLength = this.$options.data().listLength;
          updateFN();
        }, 1000);
      },
      // 上拉加载
      onPullingUp(updateFN) {
        setTimeout(() => {
          if (this.listLength < 50) this.listLength += 10;
          updateFN();
        }, 1000);
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  .list {
    padding: 0 15px;
    li {
      display: flex;
      align-items: center;
      padding: 15px 0;
      &:not(:last-of-type) {
        border-bottom: $border;
      }
      img {
        flex: 0 0 50px;
        width: 50px;
        margin-right: 10px;
        border-radius: 5px;
      }
      > div {
        .name {
          font-size: 14px;
          margin-bottom: 10px;
        }
        .desc {
          font-size: 12px;
          color: #aaa;
        }
      }
    }
  }

</style>
