<template>
  <detail
    ref="detail"
    :title="title">
    <div class="wrapper" slot="main">
      <div class="content" v-html="content"></div>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "protocol",
    data() {
      return {
        content: ""
      };
    },
    computed: {
      title() {
        return this.$route.query.title || "";
      }
    },
    methods: {
      init() {
        this.content = this.title.repeat(100);
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  .wrapper {
    padding: 15px;
    .time {
      font-size: 12px;
      color: $subFontColor;
      margin-bottom: 30px;
    }
    .content {
      font-size: 16px;
      line-height: 30px;
    }
  }

</style>
