<template>
  <cube-scroll
    class="cube-scroll"
    ref="scroll"
    v-bind="$attrs"
    v-on="$listeners"
    :options="options"
    @pulling-down="onPullingDown"
    @pulling-up="onPullingUp">
    <slot name="content"></slot>
  </cube-scroll>
</template>

<script>
  import mixinScrollConfig from "@/utils/mixinScrollConfig";

  export default {
    name: "scroll",
    mixins: [mixinScrollConfig]
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .cube-scroll {
    ::v-deep {
      .cube-pulldown-loaded span, .cube-pullup-wrapper .before-trigger span {
        font-size: 16px;
        font-weight: bold;
      }
    }
  }
</style>
