<template>
  <detail
    ref="detail"
    type="scroll"
    title="公告"
    :data="list"
    @onPullingDown="onPullingDown"
    @onPullingUp="onPullingUp">
    <div slot="header-right" class="read-all active-action">
      <i class="iconfont iconclean"></i>
      <p>全部已读</p>
    </div>
    <ul class="list" slot="main">
      <li v-for="item in 20" :key="item" @click="routerPush('/announcement-detail')">
        <p class="time">2020-06-06 12:00:00</p>
        <div class="wrapper">
          <i class="iconfont icondot"></i>
          <div class="title">升级通知</div>
          <div class="desc active-action">
            <p>了解详情</p>
            <i class="iconfont iconarrowright"></i>
          </div>
        </div>
      </li>
    </ul>
  </detail>
</template>

<script>
  export default {
    name: "announcement",
    data() {
      return {
        list: []
      };
    },
    methods: {
      init() {},
      // 刷新滚动高度
      refreshScroll() {
        this.$refs.detail.refreshScroll();
      },
      // 下拉刷新
      onPullingDown(updateFN) {
        setTimeout(() => {
          updateFN();
        }, 1000);
      },
      // 上拉加载
      onPullingUp(updateFN) {
        setTimeout(() => {
          updateFN();
        }, 1000);
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  .read-all {
    background-color: $bodyBackgroundColor;
    padding: 10px 15px;
    border-radius: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    .iconfont {
      font-size: 14px;
      margin-right: 5px;
    }
    p {
      font-size: 14px;
    }
  }

  .list {
    padding: 15px;
    li {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 15px;
      .time {
        font-size: 12px;
        background-color: $bodyBackgroundColor;
        padding: 5px 10px;
        border-radius: 5px;
        color: $subFontColor;
        margin-bottom: 10px;
      }
      .wrapper {
        width: 100%;
        border: $border;
        border-radius: 10px;
        position: relative;
        box-shadow: $boxShadow;
        .icondot {
          position: absolute;
          right: 5px;
          top: 5px;
          font-size: 20px;
          color: $red;
        }
        .title {
          font-size: 18px;
          font-weight: bold;
          border-bottom: $border;
          padding: 20px 15px;
        }
        .desc {
          font-size: 14px;
          color: $subFontColor;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 18px 15px;
          .iconfont {
            font-size: 14px;
          }
        }
      }
    }
  }

</style>
