export default {
  data() {
    return {};
  },
  methods: {
    clear() {
      Object.assign(this.$data, this.$options.data());
    },
    routerPush(path) {
      if (!path) return;
      if (this.$router.getMatchedComponents(path).length === 0) {
        const toast = this.$createToast({
          time: 1000,
          type: "error",
          txt: "页面不存在"
        });
        toast.show();
        return;
      }
      this.$router.push(path);
    }
  }
};
