<template>
  <div class="form-custom">
    <div class="form-custom-item" v-for="(item, idx) in items" :key="idx">
      <div class="form-custom-item-label">{{item.label}}</div>
      <div class="form-custom-item-content">
        <template v-if="['cube-input','cube-select'].includes(item.type)">
          <component :is="item.type" @focus="focus" v-bind="item.props" v-model="model[item.modelKey]"></component>
        </template>
        <template v-if="['p','div'].includes(item.type)">
          <component :is="item.type" v-bind="item.props" v-html="model[item.modelKey]"></component>
        </template>
        <template v-if="['slot'].includes(item.type)">
          <slot :name="item.slotName||item.modelKey"></slot>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "form-custom",
    props: {
      items: {
        type: Array,
        default: () => []
      },
      model: {
        type: Object,
        default: () => {
        }
      }
    },
    data() {
      return {};
    },
    methods: {
      init() {
      },
      focus(e) {
        if (e.target.readOnly) return;
        this.$nextTick(() => {
          setTimeout(() => {
            e.target.scrollIntoView();
          }, 300);
        });
      }
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .form-custom {
    &-item {
      display: flex;
      align-items: center;
      min-height: 50px;
      border-bottom: $border;
      padding: 0 5px;
      &-label {
        font-size: 16px;
        flex: 0 0 100px;
      }
      &-content {
        font-size: 16px;
        width: 100%;
        color: $subFontColor;
        ::v-deep {
          .cube-input, .cube-select {
            &:after {
              opacity: 0;
            }
          }
          .cube-select, .cube-select-placeholder, input, input::placeholder {
            color: $subFontColor;
          }
          .cube-select-placeholder, input::placeholder {
            opacity: 0.3;
          }
        }
      }
    }
  }

</style>
