import Vue from "vue";

Vue.prototype.transition = "";

export default {
  ready: function () {
    Vue.prototype.transition = "";
    this.set("slide-into");
  },
  set: function (val, time) {
    if (time === 0) {
      Vue.prototype.transition = val;
    } else {
      setTimeout(() => {
        Vue.prototype.transition = val;
      }, 10);
    }
  }
};
