<template>
  <div class="find">
    <app-header :border="Boolean(scrollTop)">
      <cube-select slot="left" placeholder="选择钱包" v-model="walletVal" :options="walletList"></cube-select>
      <p slot="content">发现</p>
      <div slot="right" class="icons">
        <i class="iconfont iconscan active-action"></i>
        <i class="iconfont iconsearch active-action"></i>
      </div>
    </app-header>

    <main ref="scrollTarget" @scroll="scroll">
      <div class="top">
        <!--轮播图-->
        <div class="cube-slide-img">
          <cube-slide :data="slideList"/>
        </div>
        <ul class="nav">
          <li class="active-action">
            <i class="iconfont icondefi"></i>
            <p>DeFi</p>
          </li>
          <li class="active-action" @click="routerPush('/exchange')">
            <i class="iconfont iconexchange2"></i>
            <p>兑换</p>
          </li>
          <li class="active-action">
            <i class="iconfont iconstaking"></i>
            <p>Staking</p>
          </li>
        </ul>
      </div>

      <div class="list-wrapper" v-for="(item, idx) in softwareTypeList" :key="idx">
        <div class="title">
          <p>{{item.name}}</p>
          <p class="all active-action" @click="routerPush(item.path)">全部</p>
        </div>
        <ul>
          <li class="active-action" v-for="item in 6" :key="item">
            <img src="@/assets/images/soft.png">
            <div>
              <p class="name">ChinaZ Arena</p>
              <p class="desc">放置与挖矿，玩与赚</p>
            </div>
          </li>
        </ul>
      </div>
    </main>
  </div>
</template>

<script>
  import mixinScrollToHeight from "@/utils/mixinScrollToHeight";

  export default {
    name: "find",
    mixins: [mixinScrollToHeight],
    data() {
      return {
        walletList: [2013, 2014, 2015, 2016666666666666666, 2017, 2018],
        walletVal: "",
        slideList: [
          {
            url: "",
            image: require("@/assets/images/find/banner.png")
          },
          {
            url: "",
            image: require("@/assets/images/find/banner.png")
          }
        ]
      };
    },
    computed: {
      softwareTypeList() {
        return [
          {
            name: "推荐应用",
            path: "/software?name=推荐应用"
          },
          {
            name: "区块浏览器",
            path: "/software?name=区块浏览器"
          }
        ];
      }
    },
    methods: {},
    activated() {
    }
  };
</script>

<style scoped lang="scss">
  $boxShadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
  .find {
    .app-header {
      .cube-select {
        background-color: transparent;
        border: $border;
        border-radius: 10px;
        width: 90px;
        padding-top: 8px;
        padding-bottom: 8px;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        &:after {
          border: none;
        }
      }
      .icons {
        font-size: 0;
        .iconfont {
          font-size: 20px;
          &.iconscan {
            margin-right: 20px;
          }
        }
      }
    }
    .top {
      @include backgroundWhite;
      box-shadow: $boxShadow;
      margin-bottom: 15px;
      .cube-slide-img {
        padding: 15px;
        height: 150px;
        border-radius: 5px;
        overflow: hidden;
      }
      .nav {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 15px 30px 30px;
        li {
          flex: 1;
          text-align: center;
          .iconfont {
            font-size: 40px;
            color: $blue;
          }
          p {
            font-size: 14px;
            margin-top: 10px;
          }
        }
      }
    }
    .list-wrapper {
      background-color: $mainBackgroundColor;
      box-shadow: $boxShadow;
      padding: 0 15px;
      margin-bottom: 15px;
      .title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        p {
          font-size: 16px;
          line-height: 50px;
          &.all {
            font-size: 14px;
            color: #aaa;
          }
        }
      }
      ul {
        li {
          display: flex;
          align-items: center;
          padding: 15px 0;
          border-top: $border;
          img {
            flex: 0 0 50px;
            width: 50px;
            margin-right: 10px;
            border-radius: 5px;
          }
          > div {
            .name {
              font-size: 14px;
              margin-bottom: 10px;
            }
            .desc {
              font-size: 12px;
              color: #aaa;
            }
          }
        }
      }
    }
  }

</style>
