<template>
  <div class="app-header" :style="style">
    <div class="app-bar" :style="appBarStyle"></div>
    <div class="app-header-main">
      <div class="app-header-main-left">
        <slot name="left"></slot>
      </div>
      <div class="app-header-main-content">
        <slot name="content">{{title}}</slot>
      </div>
      <div class="app-header-main-right">
        <slot name="right"></slot>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from "vuex";

  export default {
    name: "app-header",
    props: {
      title: {
        type: String,
        default: ""
      },
      border: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {};
    },
    computed: {
      style() {
        return this.border ? "box-shadow:0 0 0 1px #eaeaea" : "";
      },
      appBarStyle() {
        return `height:${this.statusBarHeight}px`;
      },
      ...mapState(["statusBarHeight"])
    }
  };
</script>

<style scoped lang="scss">
  .app-header {
    @include backgroundWhite;
    width: 100%;
    position: relative;
    z-index: 3;
    .app-bar {
      width: 100%;
    }
    .app-header-main {
      position: relative;
      width: 100%;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 15px;
      font-size: 0;
      &-left, &-right {
        position: relative;
        z-index: 1;
      }
      .app-header-main-content {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        font-size: 18px;
        line-height: 50px;
        text-align: center;
        p, .iconfont {
          font-family: DIN-Black-2, "Microsoft YaHei UI", cursive;
        }
      }
    }
  }
</style>
