<template>
  <detail title="修改密码" class="change-password" fixedTitle>
    <cube-tab-bar v-model="activeTab" show-slider slot="section">
      <cube-tab v-for="(item, index) in tabs" :icon="item.icon" :label="item.label" :key="index">
      </cube-tab>
    </cube-tab-bar>
    <cube-tab-panels v-model="activeTab" slot="main">
      <cube-tab-panel label="登录密码">
        <form-custom :model="psw" :items="itemsPsw"></form-custom>
        <submit-button @click.native="submitPsw"></submit-button>
      </cube-tab-panel>
      <cube-tab-panel label="安全密码">
        <form-custom :model="safePsw" :items="itemsSafePsw"></form-custom>
        <submit-button @click.native="submitSafePsw"></submit-button>
      </cube-tab-panel>
    </cube-tab-panels>
  </detail>
</template>

<script>
  export default {
    name: "change-password",
    data() {
      return {
        activeTab: "登录密码",
        psw: {
          old: "",
          new: "",
          cfNew: ""
        },
        safePsw: {
          old: "",
          new: "",
          cfNew: ""
        }
      };
    },
    computed: {
      tabs() {
        return [
          {
            label: "登录密码"
          },
          {
            label: "安全密码"
          }
        ];
      },
      itemsPsw() {
        return [
          {
            type: "cube-input",
            modelKey: "old",
            label: "原密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "new",
            label: "新密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "cfNew",
            label: "确认新密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      },
      itemsSafePsw() {
        return [
          {
            type: "cube-input",
            modelKey: "old",
            label: "原密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "new",
            label: "新密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          },
          {
            type: "cube-input",
            modelKey: "cfNew",
            label: "确认新密码",
            props: {
              placeholder: "请输入",
              type: "password",
              eye: {
                open: false
              },
              clearable: true
            }
          }
        ];
      }
    },
    methods: {
      init() {
        this.clear();
      },
      submitPsw() {
        console.log(this.psw);
      },
      submitSafePsw() {
        console.log(this.safePsw);
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssUnknownTarget -->
<style scoped lang="scss">
  @import "~@/assets/scss/cube-tab-bar.scss";
</style>
