<template>
  <detail title="谷歌验证码">
    <div slot="main" class="google-code">
      <ul class="list">
        <li v-for="(item, idx) in code"
            :key="idx"
            :class="{'active':idx===activeIdx}"
            v-text="idx<activeIdx?value[idx]:''"
            @click="focus"></li>
      </ul>
      <input type="number" ref="input" v-model="value"/>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "google-code",
    data() {
      return {
        code: new Array(6),
        activeIdx: 0,
        value: ""
      };
    },
    watch: {
      value(n, o) {
        if (n.length === 6 && n !== o.slice(0, 6)) {
          this.submit();
        }
        if (n.length > 6) {
          this.value = this.value.slice(0, 6);
        }
        this.activeIdx = n.length;
      }
    },
    methods: {
      init() {
        this.clear();
      },
      focus() {
        this.$refs.input.focus();
      },
      submit() {
        console.log(this.value);
        this.$toast({
          type: "correct",
          text: "验证成功"
        });
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  @keyframes flashing {
    0% {
      opacity: 1;
    }
    49% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 0;
    }
  }

  .google-code {
    .list {
      display: flex;
      padding: 40px 20px;
      li {
        flex: 1;
        height: 40px;
        border-bottom: $border;
        margin-right: 15px;
        font-size: 30px;
        font-weight: bold;
        text-align: center;
        padding: 5px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        &.active {
          border-bottom: 2px solid $blue;
          &:before {
            content: "";
            display: block;
            width: 4px;
            height: 100%;
            background-color: $blue;
            animation: flashing 1s infinite;
          }
        }
      }
    }
    input {
      border: $border;
      opacity: 0;
      height: 0;
      position: absolute;
      bottom: 0;
    }
  }

</style>
