import Vue from "vue";
import Vuex from "vuex";
import types from "./mutation-types";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    statusBarHeight: 0, // 状态栏高度
    isBack: false, // 判断是否是返回操作
    user: {}, // 用户信息
  },
  mutations: {
    [types.SET_STATUS_BAR_HEIGHT](state, data) {
      if (!data || isNaN(data)) return;
      state.statusBarHeight = Number(data);
    },
    [types.SET_IS_BACK](state, data) {
      state.isBack = Boolean(data);
    },
    [types.SET_USER](state, data) {
      sessionStorage.setItem("user", JSON.stringify(data));
      state.user = data;
    }
  },
  actions: {},
  modules: {}
});
