<template>
  <div class="assets" @click="optsVisible=false">
    <app-header :border="false">
      <i slot="left" class="iconfont iconring active-action" @click="routerPush('/announcement')"></i>
      <div slot="content" class="title">
        <i class="iconfont iconlogo"></i>
        <p>NOBE MINE</p>
      </div>
      <div slot="right" class="opts">
        <i class="iconfont iconminus active-action" @click.stop="changeOptsState"></i>
        <ul class="opts-list" v-if="optsVisible">
          <li @click="routerPush('/personal-information')">个人资料</li>
          <li @click="routerPush('/change-password')">修改密码</li>
        </ul>
      </div>
    </app-header>

    <main ref="scrollTarget" @scroll="scrollCustom">
      <div class="top">
        <div class="assets-total">
          <div class="title">
            <p>总资产</p>
            <i class="iconfont" :class="eyeVisible?'iconeye-open':'iconeye-close'" @click="changeEyeState"></i>
          </div>
          <div class="content">
            <p>{{handleMoney("0.00")}}</p>
            <p class="unit" v-if="eyeVisible">¥</p>
          </div>
        </div>
      </div>

      <div class="assets-mine">
        <p class="title">我的资产</p>
        <ul>
          <li class="active-action" v-for="(item) in 9" :key="item" @click="toAssetsDetail">
            <img src="@/assets/images/Bitcoin.png">
            <div class="content">
              <div class="coin-name">
                <p>SLB</p>
              </div>
              <div>
                <p class="balance">{{handleMoney("0.0"+item)}}</p>
                <p class="ex">{{handleMoney("≈¥0.00")}}</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </main>
  </div>
</template>

<script>
  import mixinScrollToHeight from "@/utils/mixinScrollToHeight";

  export default {
    name: "assets",
    mixins: [mixinScrollToHeight],
    data() {
      return {
        optsVisible: false,
        eyeVisible: true
      };
    },
    methods: {
      init() {
      },
      scrollCustom(e) {
        this.scroll(e);
        this.optsVisible = false;
      },
      changeOptsState() {
        this.optsVisible = !this.optsVisible;
      },
      changeEyeState() {
        this.eyeVisible = !this.eyeVisible;
      },
      handleMoney(val) {
        return this.eyeVisible ? val : "*****";
      },
      toAssetsDetail() {
        this.routerPush("/assets-detail");
      }
    },
    mounted() {
    },
    activated() {
      this.init();
    },
    beforeRouteLeave(to, from, next) {
      next();
    }
  };
</script>

<style scoped lang="scss">
  $padding: 15px;

  .assets {
    .app-header {
      @include backgroundBlue;
      .title {
        font-size: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        .iconfont {
          font-size: 20px;
          margin: 0 5px 0 0;
        }
      }
      .iconfont {
        font-size: 20px;
      }
      .opts {
        position: relative;
        font-size: 0;
        &-list {
          position: absolute;
          right: -15px;
          top: 35px;
          @include backgroundWhite;
          box-shadow: $boxShadow;
          padding: 10px;
          border-radius: 5px;
          &:before {
            content: "";
            display: inline-block;
            border: 6px solid;
            border-color: transparent transparent #fff transparent;
            position: absolute;
            top: -12px;
            right: 19px;
          }
          li {
            min-width: 80px;
            text-align: center;
            font-size: 12px;
            line-height: 34px;
            &:not(:last-of-type) {
              border-bottom: $border;
            }
          }
        }
      }
    }
    .top {
      @include backgroundBlue;
      box-shadow: $boxShadow;
      .assets-total {
        padding: 20px 0 40px;
        .title {
          display: flex;
          align-items: center;
          justify-content: center;
          p {
            font-size: 14px;
          }
          .iconfont {
            display: inline-block;
            padding: 8px;
          }
        }
        .content {
          display: flex;
          align-items: flex-start;
          justify-content: center;
          margin-top: 10px;
          font-size: 46px;
          p {
            &:not(.unit) {
              @include numberFontFamily;
            }
          }
          .unit {
            font-size: 20px;
            margin-left: 10px;
          }
        }
      }
    }
    .assets-mine {
      border-radius: 10px;
      overflow: hidden;
      position: relative;
      top: -10px;
      z-index: 1;
      background-color: $bodyBackgroundColor;
      .title {
        padding: $padding;
        font-size: 16px;
      }
      ul {
        @include backgroundWhite;
        box-shadow: $boxShadow;
        li {
          padding: 0 $padding;
          display: flex;
          align-items: center;
          &:not(:last-of-type) {
            .content {
              border-bottom: $border;
            }
          }
          img {
            width: 30px;
            height: 30px;
            margin-right: 10px;
            flex: 0 0 30px;
          }
          .content {
            padding: $padding 0;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .coin-name {
              display: flex;
              align-items: center;
              p {
                font-size: 18px;
                font-weight: bold;
                font-family: DIN-Black-2, "Microsoft YaHei UI", cursive;
              }
            }

            .balance {
              text-align: right;
              font-size: 20px;
              @include numberFontFamily;
            }

            .ex {
              text-align: right;
              font-size: 12px;
              margin-top: 5px;
            }
          }
        }
      }
    }
  }

</style>
