<template>
  <detail title="邮箱绑定">
    <div slot="main" class="bind-email">
      <form>
        <div class="form-item">
          <cube-input
            type="text"
            placeholder="请输入邮箱"
            clearable
            v-model="model.email"></cube-input>
        </div>
      </form>
      <submit-button text="下一步" :disabled="disabled"></submit-button>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "bind-email",
    data() {
      return {
        model: {
          email: ""
        }
      };
    },
    computed: {
      disabled() {
        const flag = Boolean(this.model.email);
        return !flag;
      }
    },
    methods: {
      init() {
        this.clear();
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .bind-email {
    padding: $padding;
    .tips {
      font-size: 12px;
      color: $red;
      display: flex;
      align-items: center;
    }
    form {
      margin-top: 30px;
      .form-item {
        margin-bottom: 15px;
        .cube-input {
          border-bottom: 1px solid $subFontColor;
          &:after {
            opacity: 0;
          }
          &.cube-input_active {
            border-color: $blue;
          }
          ::v-deep {
            input {
              padding-left: 0;
              padding-right: 0;
              font-size: 16px;
              &::placeholder {
                color: $subFontColor;
              }
            }
          }
        }
      }
    }
  }

</style>
