import store from "@/store";

// 需要用户权限的路由
const userRoute = ["/setup", "/personal-information"];

export function checkUser(to) {
  return new Promise((resolve, reject) => {
    if (to.matched.some(route => userRoute.includes(route.path))) {
      if (!store.state.user.username) {
        let user = sessionStorage.getItem("user");
        if (!user) {
          reject(to);
        }
        user = JSON.parse(user);
        if (!user.username) {
          reject(to);
        }
        store.commit("setUser", user);
        resolve();
      }
      resolve();
    }
    resolve();
  });
}
