<template>
  <div class="table-scroll">
    <ul class="title">
      <li v-for="(item, idx) in tableHeader" :key="idx">{{item.name}}</li>
    </ul>
    <div class="list-wrapper">
      <scroll
        ref="scroll"
        v-bind="$attrs"
        v-on="$listeners"
        :pullingDown="pullingDown"
        :pullingUp="pullingUp">
        <ul slot="content">
          <li v-for="(item, idx) in tableData" :key="idx">
            <slot :name="header.key" v-for="(header, idx) in tableHeader" :row="item">
              <div :key="idx">{{item[header.key]}}</div>
            </slot>
          </li>
        </ul>
      </scroll>
    </div>
  </div>
</template>

<script>
  export default {
    name: "table-scroll",
    props: {
      tableHeader: {
        type: Array,
        default: () => []
      },
      tableData: {
        type: Array,
        default: () => []
      }
    },
    computed: {
      pullingDown() {
        return !!this.$listeners.onPullingDown;
      },
      pullingUp() {
        return !!this.$listeners.onPullingUp;
      }
    },
    methods: {}
  };
</script>

<style scoped lang="scss">
  .table-scroll {
    font-size: 14px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .title {
      background-color: $bodyBackgroundColor;
      flex: 0 0 40px;
      display: flex;
      align-items: center;
    }
    .list-wrapper {
      height: 100%;
      overflow-x: hidden;
      li {
        display: flex;
        align-items: center;
      }
    }
  }

</style>
