<template>
  <div class="exchange-slg">
    <div class="current-info">
      <app-header :border="false"></app-header>
      <p class="title">当前价格</p>
      <div class="current-price">
        <p class="price">12.12345</p>
        <p class="unit">USDT</p>
      </div>
      <ul>
        <li>
          <p class="desc">SLB金额</p>
          <p class="num">1530$</p>
        </li>
        <li>
          <p class="desc">SLG股数</p>
          <p class="num">1530</p>
        </li>
        <li>
          <p class="desc">拆配次数</p>
          <p class="num">2</p>
        </li>
      </ul>
    </div>
    <!--委托-->
    <div class="border-wrapper entrust-wrapper">
      <ul class="title">
        <li class="active">
