          </div>
          <ul class="percent-list">
            <li class="active-action" v-for="item in percentList" :key="item">{{item * 100 + '%'}}</li>
          </ul>
          <div class="amount-wrapper">
            <p>可用</p>
            <p>0 SLB</p>
          </div>
          <div class="amount-wrapper">
            <p>交易额</p>
            <p>0.00000000 SLB</p>
          </div>
          <button class="active-action">买入SLG</button>
        </div>

        <div class="form-wrapper-inner">
          <div class="form-title">卖出</div>
          <div class="input-wrapper">
            <input type="text" placeholder="价格" v-limit.r/>
            <div class="arrow">
              <i class="iconfont iconarrow-up active-action"></i>
              <i class="iconfont iconarrow-down active-action"></i>
            </div>
          </div>
          <p class="ex">≈¥123456.12</p>
          <div class="input-wrapper">
            <input type="text" placeholder="数量" v-limit.r/>
            <div class="unit">SLG</div>
          </div>
          <ul class="percent-list">
            <li v-for="item in percentList" :key="item">{{item * 100 + '%'}}</li>
          </ul>
          <div class="amount-wrapper">
            <p>可用</p>
            <p>0 SLB</p>
          </div>
          <div class="amount-wrapper">
            <p>交易额</p>
            <p>0.00000000 SLB</p>
          </div>
          <button class="active-action sell">卖出SLG</button>
        </div>
      </div>
    </div>

    <!--列表-->
    <div class="border-wrapper">
      <ul class="title">
        <li v-for="(item, idx) in listTypes"
            :key="idx"
            class="active-action"
            :class="{'active':activeListType===item.id}"
            @click="changeListType(item)">
          <p>{{item.name}}</p>
        </li>
      </ul>
      <table>
        <thead>
        <tr>
          <th>玩家</th>
          <th>数量</th>
          <th>价格</th>
          <th>时间</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in 9" :key="item">
          <td>abc123</td>
          <td>8888</td>
          <td>123.123456</td>
          <td>2020-08-08</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: "exchange-slg",
    data() {
      return {
        percentList: [0.25, 0.5, 0.75, 1],
        listTypes: [
          {
            id: 0,
            name: "卖出列表"
          },
          {
            id: 1,
            name: "买入列表"
          },
          {
            id: 2,
            name: "卖出记录"
          },
          {
            id: 3,
            name: "买入记录"
          }
        ],
        activeListType: 0
      };
    },
    methods: {
      init() {
      },
      changeListType(item) {
        this.activeListType = item.id;
      }
    }
  };
</script>

<style scoped lang="scss">
  .exchange-slg {
    .current-info {
      @include backgroundBlue;
      padding: 0 30px 30px;
      .app-header {
        background-color: inherit;
      }
      .title {
        padding: 20px 0;
        font-weight: bold;
        font-size: 16px;
      }
      .current-price {
        display: flex;
        align-items: flex-end;
        transform: skewX(-15deg);
        position: relative;

        &:before {
          content: "";
          display: inline-block;
          width: 70%;
          height: 20px;
          border: 2px solid green;
          border-image: linear-gradient(to top right, $green, $blue, $blue) 2 2;
          transform: skewX(-20deg);
          position: absolute;
          left: 0;
          bottom: -5px;
        }

        .price {
          font-size: 28px;
          font-weight: bold;
          margin: 0 10px;
          position: relative;
          z-index: 1;
        }
        .unit {
          font-size: 12px;
          color: $grayFontColor;
        }
      }

      ul {
        display: flex;
        align-items: flex-end;
        justify-content: space-around;
        margin-top: 30px;
        transform: skewX(-15deg);

        li {
          flex: 1;
          position: relative;

          &:before {
            content: "";
            display: inline-block;
            width: 80%;
            height: 15px;
            border: 2px solid green;
            border-image: linear-gradient(to top right, $green, $blue, $blue) 2 2;
            transform: skewX(-20deg);
            position: absolute;
            left: 10px;
            bottom: -6px;
          }

          .desc {
            color: $grayFontColor;
            font-weight: bold;
            font-size: 12px;
          }
          .num {
            font-size: 14px;
            margin-top: 10px;
            font-weight: bold;
            position: relative;
            text-align: center;
          }
        }
      }
    }

    .border-wrapper {
      border: 2px solid green;
      border-image: linear-gradient(to left bottom, $green, $mainBackgroundColor, $mainBackgroundColor) 2 2;
      .title {
        display: flex;
        align-items: center;
        padding-left: 10px;

        li {
          padding: 10px;
          transform: skewX(-35deg);

          p {
            transform: skewX(25deg);
            font-size: 14px;
            font-weight: bold;
          }

          &.active {
            background: linear-gradient(to top right, $blue, $green);
            color: #fff;
          }
        }
      }

      .form-wrapper {
        padding: 15px 15px 30px;
        display: flex;
        align-items: flex-start;
        background-color: $mainBackgroundColor;

        .form-wrapper-inner {
          flex: 1;
          &:first-of-type {
            margin-right: 15px;
          }
        }

        .form-title {
          font-size: 14px;
          font-weight: bold;
          margin-bottom: 10px;
        }

        .input-wrapper {
          display: flex;
          align-items: center;
          justify-content: space-between;
          position: relative;

          input {
            background: transparent;
            font-size: 14px;
            border: $border;
            padding: 0 10px;
            height: 40px;
            width: 100%;
            &:focus {
              outline: none;
            }
          }

          .arrow {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background-color: $borderColor;
            height: 40px;
            .iconfont {
              line-height: 20px;
              font-size: 14px;
              width: 20px;
              text-align: center;
              &:active {
                background-color: $mainBackgroundColor;
              }
            }
          }

          .unit {
            position: absolute;
            right: 5px;
            top: 0;
            font-size: 14px;
            line-height: 40px;
          }
        }

        .ex {
          font-size: 12px;
          color: $grayFontColor;
          margin: 10px 0;
        }

        .percent-list {
          display: flex;
          align-items: center;
          justify-content: space-around;
          li {
            flex: 1;
            border: $border;
            font-size: 12px;
            text-align: center;
            line-height: 30px;
            border-top: none;
            &:not(:last-of-type) {
              border-right: none;
            }
          }
        }

        .amount-wrapper {
          display: flex;
          align-items: flex-start;
          justify-content: space-between;
          font-size: 12px;
          margin: 10px 0;
        }

        button {
          font-size: 14px;
          width: 100%;
          height: 40px;
          background-color: $green;
          border: none;
          color: #fff;
          box-shadow: $boxShadow;
          &:focus {
            outline: none;
          }
          &.sell {
            background-color: $red;
          }
        }
      }

      table {
        font-size: 12px;
        width: 100%;
        background-color: $mainBackgroundColor;

        th, td {
          padding: 10px 0;
          text-align: center;
        }
      }
    }
  }

</style>
