import {mapState} from "vuex";

export default {
  data() {
    return {
      scrollTop: 0
    };
  },
  computed: {
    scrollTarget() {
      return this.$refs.scrollTarget;
    },
    ...mapState(["isBack"])
  },
  watch: {
    isBack(n) {
      if (!this.scrollTarget) return;
      if (!n) return;
      const {posY} = this.$route.meta;
      if (isNaN(posY)) return;
      this.scrollTarget.scrollTo(0, posY);
    }
  },
  methods: {
    scroll(e) {
      this.scrollTop = e.target.scrollTop;
    }
  },
  beforeRouteLeave(to, from, next) {
    if (this.scrollTarget) {
      this.$route.meta.posY = this.scrollTarget.scrollTop;
    }
    next();
  }
};
