<template>
  <detail title="找回密码">
    <div slot="main" class="retrieve-password">
      <div class="tips">
        <i class="iconfont icontips"></i>
        <p>找回密码后，24小时内无法提币</p>
      </div>
      <form>
        <div class="form-item">
          <cube-input
            type="text"
            placeholder="邮箱或手机号"
            clearable
            v-model="model.username"></cube-input>
        </div>
      </form>
      <submit-button text="下一步" :disabled="disabled"></submit-button>
    </div>
  </detail>
</template>

<script>
  export default {
    name: "retrieve-password",
    data() {
      return {
        model: {
          username: ""
        }
      };
    },
    computed: {
      disabled() {
        const flag = Boolean(this.model.username);
        return !flag;
      }
    },
    methods: {
      init() {
        this.clear();
      }
    },
    activated() {
      this.init();
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .retrieve-password {
    padding: $padding;
    .tips {
      font-size: 12px;
      color: $red;
      display: flex;
      align-items: center;
    }
    form {
      margin-top: 30px;
      .form-item {
        margin-bottom: 15px;
        .cube-input {
          border-bottom: 1px solid $subFontColor;
          &:after {
            opacity: 0;
          }
          &.cube-input_active {
            border-color: $blue;
          }
          ::v-deep {
            input {
              padding-left: 0;
              padding-right: 0;
              font-size: 16px;
              &::placeholder {
                color: $subFontColor;
              }
            }
          }
        }
      }
    }
  }

</style>
