<template>
  <div class="market">
    <app-header :border="Boolean(scrollTop)">
      <i slot="left"></i>
      <div slot="content" class="title">
        <i class="iconfont iconlogo"></i>
        <p>NOBE MINE</p>
      </div>
      <i slot="right" class="iconfont iconscan active-action" @click="toFlutter"></i>
    </app-header>
    <main ref="scrollTarget" @scroll="scroll">
      <div class="top">
        <!--轮播图-->
        <div class="cube-slide-img">
          <cube-slide :data="slideList"/>
        </div>
        <!--公告-->
        <div class="cube-slide-text">
          <i class="iconfont icondiqiu"></i>
          <div class="cube-slide-text-inner">
            <cube-slide direction="vertical"
                        :data="noticeList"
                        :showDots="false">
              <cube-slide-item v-for="(item, idx) in noticeList" :key="idx" @click.native="toNotice(item)">
                <p @click="routerPush('/announcement-detail')">{{item.text}}</p>
              </cube-slide-item>
            </cube-slide>
          </div>
          <i class="iconfont iconcaidan active-action" @click="routerPush('/announcement')"></i>
        </div>
        <!--导航-->
        <ul class="nav">
          <li class="active-action" @click="routerPush('/reg')">
            <div class="icon-wrapper">
              <i class="iconfont iconaccount"></i>
            </div>
            <p>玩家开户</p>
          </li>
          <li class="active-action" @click="routerPush('/reg-sub')">
            <div class="icon-wrapper">
              <i class="iconfont iconaccount2"></i>
            </div>
            <p>二次开户</p>
          </li>
          <li class="active-action" @click="routerPush('/exchange?id=2')">
            <div class="icon-wrapper">
              <i class="iconfont icontransfer"></i>
            </div>
            <p>转账</p>
          </li>
          <li class="active-action" @click="routerPush('/exchange')">
            <div class="icon-wrapper">
              <i class="iconfont iconexchange"></i>
            </div>
            <p>兑换</p>
          </li>
        </ul>
      </div>

      <div class="middle">
        <div class="left">
          <div class="title active-action" @click="routerPush('/assets')">
            <div>
              <i class="iconfont iconassets"></i>
              <p>我的资产</p>
            </div>
            <i class="iconfont iconarrowright"></i>
          </div>
          <div class="content">
            <p>123456.123</p>
            <p class="unit">USDN</p>
          </div>
        </div>

        <div class="right">
          <div class="active-action" @click="routerPush('/upgrade?id=3')">
            <i class="iconfont iconupgrade"></i>
            <p>账户升级</p>
          </div>
          <div class="active-action" @click="routerPush('/upgrade')">
            <i class="iconfont iconapply"></i>
            <p>特权申请</p>
          </div>
        </div>
      </div>

      <div class="invite">
        <img src="@/assets/images/market/invite.png">
      </div>

      <div class="market-mine">
        <p class="title">我的市场</p>
        <ul>
          <li v-for="(item, idx) in marketMineList" :key="idx" @click="routerPush(item.path)">
            <i class="iconfont" :class="item.icon"></i>
            <div>
              <p>{{item.name}}</p>
              <i class="iconfont iconarrowright"></i>
            </div>
          </li>
        </ul>
      </div>
    </main>
  </div>
</template>

<script>
  import mixinScrollToHeight from "@/utils/mixinScrollToHeight";

  export default {
    name: "market",
    mixins: [mixinScrollToHeight],
    data() {
      return {
        slideList: [
          {
            url: "",
            image: require("@/assets/images/slideImg.jpg")
          },
          {
            url: "",
            image: require("@/assets/images/slideImg.jpg")
          }
        ],
        noticeList: [
          {
            id: 1,
            text: "公告1"
          },
          {
            id: 2,
            text: "公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2"
          },
          {
            id: 3,
            text: "公告3"
          }
        ]
      };
    },
    computed: {
      marketMineList() {
        return [
          {
            name: "网站公告",
            icon: "iconassets",
            path: "/announcement"
          },
          {
            name: "推荐列表",
            icon: "iconassets",
            path: "/recommend"
          },
          {
            name: "子账号列表",
            icon: "iconassets",
            path: "/sub-account"
          },
          {
            name: "奖金列表",
            icon: "iconassets",
            path: "/bonus"
          },
          {
            name: "推荐网",
            icon: "iconassets",
            path: "/recommend-net"
          },
          {
            name: "管理网",
            icon: "iconassets",
            path: "/management-net"
          }
        ];
      }
    },
    methods: {
      init() {
      },
      toNotice(item) {
      },
      toFlutter() {
        window.Toaster.postMessage(JSON.stringify({
          name: "ddd",
          psw: "111"
        }));
      }
    },
    mounted() {
      this.init();
    }
  };
</script>

<style scoped lang="scss">
  .market {
    .app-header {
      .title {
        font-size: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        .iconfont {
          font-size: 20px;
          margin-right: 5px;
        }
      }
      .iconscan {
        font-size: 20px;
      }
    }
    .top {
      @include backgroundWhite;
      padding-bottom: 20px;
      .cube-slide-img {
        height: 200px;
        margin: 0 15px 15px;
        border-radius: 5px;
        overflow: hidden;
      }

      .cube-slide-text {
        background-color: $bodyBackgroundColor;
        margin: 15px;
        height: 40px;
        padding: 0 10px;
        border-radius: 5px;
        overflow: hidden;
        position: relative;
        font-size: 0;
        .cube-slide-text-inner {
          padding: 0 20px;
          height: 100%;
          .cube-slide-item {
            height: 40px;
            font-size: 14px;
            line-height: 40px;
            p {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
        }
        .icondiqiu {
          position: absolute;
          left: 5px;
          top: 0;
          font-size: 20px;
          line-height: 40px;
        }
        .iconcaidan {
          position: absolute;
          right: 10px;
          top: 0;
          font-size: 20px;
          line-height: 40px;
        }
      }

      .nav {
        display: flex;
        align-items: center;
        justify-content: space-around;
        li {
          flex: 1;
          text-align: center;

          .icon-wrapper {
            width: 50px;
            height: 50px;
            background-color: $bodyBackgroundColor;
            box-shadow: 2px 3px 6px 0 rgba(0, 0, 0, 0.3);
            border-radius: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: auto;

            .iconfont {
              font-size: 20px;
              color: $blue;
              &.iconexchange {
                font-size: 24px;
              }
            }
          }

          p {
            font-size: 12px;
            margin-top: 10px;
          }
        }
      }
    }

    .middle {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      margin: 15px;
      height: 120px;
      .left {
        flex: 4;
        height: 100%;
        margin-right: 15px;
        @include backgroundWhite;
        box-shadow: $boxShadow;
        border-radius: 5px;
        padding: 0 15px;
        .title {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 15px 0;
          border-bottom: $border;
          > div {
            display: flex;
            align-items: center;
            .iconfont {
              font-size: 20px;
              margin-right: 5px;
              color: $blue;
            }
            p {
              font-size: 16px;
            }
          }
          > .iconfont {
            font-size: 14px;
            color: #ccc;
          }
        }
        .content {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 24px 0;
          p {
            font-size: 16px;
            color: $blue;
          }
        }
      }
      .right {
        flex: 3;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        > div {
          flex: 1;
          width: 100%;
          @include backgroundWhite;
          box-shadow: $boxShadow;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 5px;
          &:first-of-type {
            margin-bottom: 15px;
          }
          .iconfont {
            font-size: 20px;
            margin-right: 5px;
            color: $blue;
          }
          p {
            font-size: 16px;
            line-height: 50px;
          }
        }
      }
    }

    .invite {
      margin: 15px;
      img {
        width: 100%;
      }
    }

    .market-mine {
      @include backgroundWhite;
      .title {
        font-size: 16px;
        padding: 15px;
      }
      ul {
        li {
          display: flex;
          align-items: center;
          padding: 0 15px;
          &:last-of-type {
            div {
              border: none;
            }
          }
          &:active {
            opacity: 0.95;
          }
          > .iconfont {
            font-size: 20px;
            color: $blue;
            margin-right: 10px;
          }
          > div {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            border-bottom: $border;
            p {
              font-size: 16px;
              line-height: 50px;
            }
            .iconfont {
              font-size: 14px;
              color: #999;
            }
          }
        }
      }
    }
  }

</style>
