<template>
  <detail title="计价货币">
    <ul slot="main" class="list">
      <li class="active-action" v-for="(item, idx) in list" :key="idx" @click="change(item)">
        <p>{{item.label}}</p>
        <i class="iconfont iconchecked" v-if="item.value===current"></i>
      </li>
    </ul>
  </detail>
</template>

<script>
  export default {
    name: "denomination-currency",
    data() {
      return {
        current: 0
      };
    },
    computed: {
      list() {
        const list = ["人民币(CNY)", "美元(USD)", "韩元(KRW)", "越南盾(VND)"];
        return list.map((label, value) => {
          return {
            label,
            value
          };
        });
      }
    },
    methods: {
      init() {
      },
      change(item) {
        this.current = item.value;
        setTimeout(() => {
          this.$back();
        }, 300);
      }
    }
  };
</script>

<style scoped lang="scss">
  .list {
    padding: 20px 0 0 $padding;
    li {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-right: $padding;
      &:not(:last-of-type) {
        border-bottom: $border;
      }
      p {
        font-size: 16px;
        line-height: 50px;
      }
      .iconfont {
        font-size: 18px;
        color: $blue;
      }
    }
  }

</style>
