const globalComponents = require.context("@/components/global", false, /.vue$/);
const components = {
  install(Vue) {
    globalComponents.keys().forEach(key => {
      const name = key.substring(key.indexOf("/") + 1, key.lastIndexOf(".vue"));
      Vue.component(name, globalComponents(key).default);
    });
  }
};

export default components;
