<template>
  <detail
    title="推荐列表"
    type="scroll"
    ref="detail"
    :data="list"
    @onPullingDown="onPullingDown"
    @onPullingUp="onPullingUp">
    <ul slot="main" class="list">
      <li v-for="item in 20" :key="item">
        <div class="content">
          <div class="avatar">
            <img src="@/assets/images/soft.png">
          </div>
          <div class="right">
            <p class="name">用户名：user{{item}}<span>已激活</span></p>
            <div class="info">
              <p>级别：M1</p>
              <p>推荐人：user123</p>
              <p>管理人：user123</p>
              <p>区位：左区</p>
              <p>注册时间：2012-02-02</p>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </detail>
</template>

<script>
  export default {
    name: "recommend",
    data() {
      return {
        list: []
      };
    },
    methods: {
      init() {
      },
      // 刷新滚动高度
      refreshScroll() {
        this.$refs.detail.refreshScroll();
      },
      // 下拉刷新
      onPullingDown(updateFN) {
        setTimeout(() => {
          this.listLength = this.$options.data().listLength;
          updateFN();
        }, 1000);
      },
      // 上拉加载
      onPullingUp(updateFN) {
        setTimeout(() => {
          if (this.listLength < 50) this.listLength += 10;
          updateFN();
        }, 1000);
      }
    }
  };
</script>

<!--suppress CssUnknownTarget -->
<style scoped lang="scss">
  @import "~@/assets/scss/market-list.scss";
</style>
